<template>
  <CardLayout icon="local_hospital" title="Σελίδα Ασθένειας" id="Diseases">
    <template slot="content">
      <v-slide-x-transition>
        <v-card v-if="plant !== null" tile class="my-4">
          <v-card-title :class="plant.vue_color || 'secondary'" class="title white--text pa-0">
            <v-avatar tile color="white" size="55" class="ml-0 mr-2">
              <v-icon medium :color="plant.vue_color" class="mx-2">local_hospital</v-icon>
            </v-avatar>
            <span v-if="diseaseNotExist">{{ plant.gr }}</span>
            <template v-else>
              <span class="white--text">{{ plant.gr }}</span>
              <v-icon :color="plant.vue_color + ' lighten-4'">keyboard_arrow_right</v-icon>
              <span class="white--text">{{ disease.name }}</span>
            </template>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text class="pa-0">
            <v-list three-line v-if="!diseaseNotExist" class="pa-0">
              <template v-for="(info, i) in D_INFO">
                <DiseaseTile
                  :handler="() => false"
                  :key="`tile-${i}`"
                  :color="plant.vue_color"
                  :sub="info.sub"
                  :icon="info.icon">
                  <span v-html="info.title"></span>
                </DiseaseTile>
                <!-- <v-divider inset :key="`divider-${i}`"></v-divider> -->
              </template>
              <ListGroup icon="security" :color="plant.vue_color" :title="TREATMENT_TITLE" sub="Καταπολέμηση">
                <template slot="items" v-for="(treatment, t) in disease.treatment">
                  <v-list-tile :class="plant.vue_color + ' lighten-5'" :key="`tile-${t}`">
                    <v-list-tile-avatar><v-icon :color="plant.vue_color + ' lighten-3'">security</v-icon></v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title>{{ treatment }}.</v-list-tile-title>
                      <v-list-tile-sub-title>Νο.{{ t+1 }} μέτρο</v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider :class="plant.vue_color + ' lighten-3'" v-if="t+1 < disease.treatment.length" :key="`d-${t}`"></v-divider>
                </template>
              </ListGroup>
              <v-subheader>Συμπτωματολογία στο φυτό {{ plant.gr }}</v-subheader>
              <ListGroup :groupDisabled="Object.keys(disease.rules[symptom]).length == 0" v-for="(symptom, s) in Object.keys(disease.rules)" :key="`group-${s}`" :color="plant.vue_color" icon="visibility"
                :title="`Το φυτό παρουσιάζει ${symptom}`" :sub="`Κριτήρια ασθένειας για ${symptom}`" >
                <template slot="items">
                  <v-list class="pa-0">
                    <template v-for="(rule, r) in Object.keys(disease.rules[symptom])">
                      <v-list-tile :class="plant.vue_color + ' lighten-5'" :key="`tile-${s}-${r}`">
                        <v-list-tile-avatar><v-icon :color="plant.vue_color + ' lighten-3'">notes</v-icon></v-list-tile-avatar>
                        <v-list-tile-content>
                          <v-list-tile-title>{{ rule }}<v-icon class="pb-1">arrow_drop_down</v-icon></v-list-tile-title>
                          <v-list-tile-sub-title>{{ disease.rules[symptom][rule].join(' / ') }}</v-list-tile-sub-title>
                        </v-list-tile-content>
                      </v-list-tile>
                      <v-divider :class="plant.vue_color + ' lighten-3'" :key="`divider-${s}-${r}`"></v-divider>
                    </template>
                  </v-list>
                </template>
              </ListGroup>
            </v-list>
            <div v-if="diseaseNotExist">
              <v-alert
                type="error"
                transition="slide-x-transition"
                :value="true"
                class="ma-0">
                Η ασθένεια <em><b>{{ $route.params.disease }}</b></em>, δεν βρέθηκε στο αρχείο του φυτού {{ plant.gr }}.
                <br>
                Για πλήρες αρχείο ασθενειών του κηπευτικού, μεταβείτε στη Σελίδα Διάγνωσης πατώντας στο κουμπί <v-icon color="white" class="pb-1">local_florist</v-icon>.
              </v-alert>
              <v-divider :class="plant.vue_color + ' lighten-2'"></v-divider>
              <v-btn class="ma-0" depressed block large color="white" @click="toDiagnosis">
                <v-icon large :color="plant.vue_color">local_florist</v-icon>
              </v-btn>
            </div>
          </v-card-text>
        </v-card>
      </v-slide-x-transition>
      <LoadingPlants class="my-4" key="loading" v-if="plant == null"></LoadingPlants>
    </template>
  </CardLayout>
</template>

<script>
import API from '@/lib/API'
import LoadingPlants from './LoadingPlants'
import CardLayout from '@/components/layouts/CardLayout'
import DiseaseTile from '@/components/layouts/DiseaseTile'
import ListGroup from '@/components/layouts/ListGroup'
import { bus } from '@/lib/bus'

export default {
  name: 'Diseases',
  components: {
    LoadingPlants,
    CardLayout,
    DiseaseTile,
    ListGroup
  },
  data () {
    return {
      plant: null,
      disease: null,
      selectedPlant: false,
      showTreatment: false
    }
  },
  created () {
    API.getPlant(this.routePlant).then(plants => {
      this.plant = plants[0]
      this.disease = this.plant.diseases.filter(disease => disease.name === this.$route.params.disease)[0]
    })
  },
  mounted () {
    bus.$on('choosed-plant', selectedPlant => {
      this.selectedPlant = selectedPlant
    })
  },
  computed: {
    TREATMENT_TITLE () {
      var count = ''
      if (this.disease.treatment.length > 1) {
        count = 'μέτρα'
      } else {
        count = 'μέτρο'
      }
      return `${this.disease.treatment.length} ${count} αντιμετώπισης`
    },
    D_RULES () {
      var rules = []
      for (var symptom in this.disease.rules) {
        for (var rule in this.disease.rules[symptom]) {
          rules.push({
            rule: rule,
            values: this.disease.rules[symptom][rule]
          })
        }
      }
      return rules
    },
    D_INFO () {
      return [{
        icon: 'info',
        title: this.disease.name,
        sub: 'Ονομασία ασθένειας'
      }, {
        icon: 'title',
        title: this.disease.parasitic ? 'Παρασιτική' : 'Μη παρασιτική',
        sub: 'Τύπος ασθένειας'
      }, {
        icon: 'bug_report',
        title: this.disease.parasitic ? `<em>${this.disease.pathogen}</em>` : this.disease.pathogen,
        sub: 'Παθογόνο αίτιο'
      }, {
        icon: 'description',
        title: this.disease.description,
        sub: 'Σύντομη περιγραφή'
      }]
    },
    routePlant () {
      return this.$route.params.plant
    },
    plantNotExist () {
      return this.plant === null
    },
    diseaseNotExist () {
      return this.plant.diseases.filter(disease => disease.name === this.$route.params.disease).length === 0
    }
  },
  methods: {
    toDiagnosis () {
      this.$router.push('/diagnosis')
    }
  }
}
</script>

<style lang="css">
.nbr{
  border-radius: 0px;
}
.ntd{
  text-decoration: none;
}
</style>



