<template>
  <CardLayout icon="home" title="Αρχική" url="/">
    <template slot="content">
      <v-card tile class="my-4">
        <v-card-text class="subheading pa-0">
          <v-carousel lazy class="elevation-1">
            <v-carousel-item
              v-for="(slide, s) in slides"
              :src="`/static/carousel/carousel_${s+1}.jpg`"
              :key="s"
              reverse-transition="scale-transition">
            </v-carousel-item>
          </v-carousel>
          <div class="pa-2">
            <h1 class="title my-2">Η εφαρμογή διάγνωσης κηπευτικών ασθενειών.</h1>
            Από εκπόνηση πτυχιακής διατριβής φοιτητή, του τμήματος Βιοτεχνολογίας, του Γεωπονικού Πανεπιστημίου Αθηνών. Το σύστημα χρησιμεύει στη διάγνωση ασθενειών κηπευτικών φυτών και δίνεται επίσης η δυνατότητα εισαγωγής δεδομένων από το χρήστη.
          </div>
        </v-card-text>
      </v-card>
    </template>
  </CardLayout>
</template>

<script>
import CardLayout from '@/components/layouts/CardLayout'
export default {
  name: 'Landing',
  components: {
    CardLayout
  },
  data () {
    return {
      dialog: false,
      icon: 'home',
      slides: 3
    }
  },
  mounted () {
  },
  methods: {
    agree: (text) => alert(text),
    disagree: text => alert(text),
    diagnose (plant) {
      this.$router.push(`/diagnosis/${plant}`)
    }
  }
}
</script>

<style>
.whitePanel{
  background-color: rgba(255,255,255,0.55);
}
.flex-title{
  background-color: rgba(0,0,0,0.75);
  cursor: pointer;
}
.bdrd{
  border-radius: 8px;
}
</style>



