<template>
  <CardLayout icon="library_books" title="Διάγνωση" url="/diagnosis">
    <template slot="content">
      <v-card v-if="plants !== null" class="transparent my-4" flat>
        <v-card-title class="pa-0 mb-2">
          <v-alert key="infoAlert" class="ma-0 alert subheading" color="light-green" v-if="routePlantNull" :value="true" type="info"
            transition="slide-y-transition">
            Με την Κοινή Ονομασία κάθε κηπευτικού, μεταφέρεστε στον αντίστοιχο σύνδεσμο του <b>Wikipedia</b>, ενώ κάθε βέλος σας μεταφέρει στη σελίδα της αντίστοιχης δραστηριότητας. Για περαιτέρω επεξεργασία της βάσης γνώσεως, μεταβείτε στη σελίδα
            Επεξεργασίας.
          </v-alert>
          <v-alert key="errorAlert" class="ma-0 alert subheading" v-else-if="plants !== null && plantNotExist" :value="true" type="error"
            transition="slide-y-transition">
            Το φυτό <b>{{ $route.params.gr }}</b> δεν βρέθηκε.<br>
            Διορθώστε την διεύθυνση <b>URL</b> και προσπαθήστε ξανά, αλλιώς ακολουθήστε τον Οδηγό
            Διάγνωσης για κάποιο από τα παρακάτω κηπευτικά.
          </v-alert>
        </v-card-title>
        <v-card-text class="pa-0">
          <transition-group key="t-group" name="slide-x-transition">
            <SelectSymptom key="1" v-if="sickPlantObject !== undefined" :sickPlant="sickPlantObject">
            </SelectSymptom>
            <PlantsCatalog key="2" v-else :plants="plants"></PlantsCatalog>
          </transition-group>
        </v-card-text>
      </v-card>
      <LoadingPlants class="my-4" v-else></LoadingPlants>
    </template>
  </CardLayout>
</template>

<script>
import API from '@/lib/API'
import SelectSymptom from '@/components/SelectSymptom'
import PlantsCatalog from '@/components/PlantsCatalog'
import LoadingPlants from '@/components/LoadingPlants'
import CardLayout from '@/components/layouts/CardLayout'

export default {
  name: 'Plants',
  components: {
    SelectSymptom,
    PlantsCatalog,
    LoadingPlants,
    CardLayout
  },
  data () {
    return {
      icon: 'local_florist',
      plants: null,
      catalog: true,
      infoAlert: true
    }
  },
  created () {
    API.getPlants().then(data => { this.plants = data }).catch(error => console.log(error))
  },
  computed: {
    sickPlantObject: function () {
      return this.plants.filter(plant => plant.gr === this.$route.params.gr)[0]
    },
    routePlantNull () {
      return (this.$route.params.gr === undefined)
    },
    plantNotExist: function () {
      return this.sickPlantObject === undefined
    }
  }
}
</script>

<style lang="css">
.ntt {
  text-transform: none;
}
.nbr{
  border-radius: 0px;
}
.fwn{
  font-weight: normal;
}
.alert{
  border: none;
}
</style>



