<template>
  <v-card id="SymptomsDialogCard" tile>
    <v-card-title class="title white--text pa-0 light-green">
      <v-avatar tile color="white" size="55" class="ml-0 mr-2">
        <v-icon medium color="light-green" class="mx-2">notes</v-icon>
      </v-avatar>
      <span>Περιγραφή Συμπτωμάτων</span>
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text class="pa-0">
      <v-list class="pa-0 transparent" three-line>
        <v-subheader class="light-green lighten-5">Το φυτό παρουσιάζει<v-icon>keyboard_arrow_right</v-icon></v-subheader>
        <template v-for="(symptom, s) in Object.keys(DESCRIBE)">
          <v-list-tile class="py-1 " :key="`tile-${s}`">
            <v-list-tile-avatar>
              <v-icon medium color="lime accent-4">visibility</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{ symptom }}</v-list-tile-title>
              <v-list-tile-sub-title>{{ DESCRIBE[symptom].description }}</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider inset :key="`topDivider-${s}`"></v-divider>
        </template>
      </v-list>
    </v-card-text>
    <v-card-actions class="justify-center py-5">
      <slot></slot>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'SymptomsDialogCard',
  props: ['plant', 'DESCRIBE'],
  data () {
    return {
    }
  }
}
</script>

<style lang="css">
</style>



